body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen","Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Ubuntu Mono", source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, button {
  font-size: 16px;
  font-family: "Source Sans Pro", sans-serif;
  line-height: 1.5;
  color: #25342F;
  /* background-color: #F0F7F5;*/
}

body {
  padding: 20px 0px;
}

/* always show up/down arrows on number inputs */
input[type=number]::-webkit-inner-spin-button {
  opacity: 1;
}

h1, h2, .main-text {
  max-width: 740px;
  padding-right: 20px;
  padding-left: 20px;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  color: #00EB5F;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 20px;
}

h1 span {
  background-color: #25342F;
  padding: 4px;
}

h1 span::before, h2::before, .command__phrase::before{
  content: '> ';
}

h2 {
  color: #00EB5F;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 2em;
  font-weight: bold;
  width: 100%;
  background-color: #25342F;
  padding: 4px;
  margin-top: 50px;
}

summary {
  font-size: 1.1em;
  margin-bottom: 35px;
  line-height: 1.6;
}

summary p {
  margin: 8px 0px;
}

em {
  color: #ED2261;
  font-weight: bold;
  font-style: normal;
}

.command {
  width: 800px;
  margin: 20px auto;
  padding-top: 10px;
  padding-bottom: 30px;
}

.command__phrase {
  font-family: 'Ubuntu Mono', monospace;
  font-weight: bold;
  font-size: 1.1em;
  text-indent: -1em;
  padding-left: 1em;
}

.msg-quote {
  font-family: "Ubuntu Mono", monospace;
  letter-spacing: 0.01em;
  color: rgb(61, 73, 69);
}
.level-indicator {
  position: fixed;
  top: 0px;
  left: 40px;
  margin: 0px auto;
  z-index: 2;
}

.level-indicator__icon, .level-indicator__reset-btn {
  width: 34px;
  display: block;
}

.level-indicator__icon {
  margin-bottom: 30px;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  height: 34px;
}

.level-indicator__reset-btn {
  margin-top: 10px;
  margin-bottom: 0px;
  cursor: pointer;
  color: grey;
  font-size: 2em;
  font-weight: bold;
  text-align: center;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

@media (max-width: 950px) {
  .level-indicator__icon, .level-indicator__reset-btn {
    display: inline-block;
    margin-bottom: 0px;
    margin-top: 10px;
    margin-right: 30px;
  }

  .level-indicator {
    width: calc(100% - 100px);
    left: 50px;
    background-color: white;
  }

}
.cipher-widget {
  font-family: 'Ubuntu Mono', monospace;
  width: 800px;
  background-color: #25342F;
  border-radius: 3px;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.cipher-widget_success {
  box-shadow: inset 0px 0px 10px #00EB5F,
              inset 0px 0px 5px #00EB5F,
              0px 0px 5px #00EB5F;
  -webkit-transition: box-shadow .5s;
  transition: box-shadow .5s;
}

.cipher-widget__title {
  letter-spacing: .05em;
  font-weight: bold;
  color: #00EB5F;
  font-size: 1.3em;
  text-transform: uppercase;
  margin: 14px 0px;
  padding: 12px 0px 0px 24px;
}

.cipher-widget__title::before {
  content: '> ';
}

.cipher-widget__body {
  padding: 24px ;
  padding-top: 10px;
}

.cipher-widget__work-section,
.cipher-widget__ref-section {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.cipher-widget__control-column {
  width: 120px;
}

.cipher-widget__key-column {
  width: 52px;
  box-sizing: border-box;
  text-align: right;
}

.cipher-widget__text-column {
  box-sizing: border-box;
}

.cipher-widget__label, .cipher-widget__subst-label,
.cipher-widget__section_title {
  margin: 0;
  font-weight: bold;
  color: white;
}

.cipher-widget__column_sliding {
  height: calc(3em + 40px);
  overflow: hidden;
}

.cipher-widget__text {
  padding: 0 5px;
  margin: 10px 0px;
  color: #00EB5F;
  /* in firefox line-hieght 'normal' resulted in
     line height larger than font size (16px vs 17px) */
  line-height: 1;
  letter-spacing: .4em;
  background-color: rgba(0, 0, 0, 0);
}

.cipher-widget__text_plain {
  color: #6AE2FA;
  text-transform: uppercase;
}

.cipher-widget__text-column .cipher-widget__text {
  padding-left: 10px;
}

.cipher-widget__text_is-neighbour {
  color: #007A31;
}

.cipher-widget__text_is-neighbour.cipher-widget__text_plain {
  color: #337380;
}

.cipher-widger__text_is-reverse::before {
  content: '-';
}

.cipher-widget__text-column.cipher-widget__column_sliding {
  border: thin solid #739B8E;
}

.cipher-widget__key-column.cipher-widget__column_sliding {
  background-color: #0A151E;
  border: thin solid #0A151E;
}

.cipher-widget__control-column .cipher-widget__key-form {
  position: relative;
  top: calc(1em + 10px);
}

.cipher-widget__key-form .cipher-widget__label {
  margin-right: 8px;
}

.cipher-widget__key-input {
  width: 54px;
  box-sizing: border-box;
  font-family: 'Ubunto Mono', monospace;
  background-color: white;
  border: thin solid black;
  color: black;
  padding: 1px;
  border-radius: 3px;
}

.cipher-widget input:focus,
.cipher-widget textarea:focus {
  outline: none;
  box-shadow: 0 0 3px #00EB5F;
}

/* sytles specific to break widget */
.cipher-widget__secret-text {
  line-height: 1.5;
  border: thin solid #739B8E;
  padding: 15px;
  letter-spacing: 0;
}

.cipher-widget__subst-table {
  margin: 10px 0px 20px 0px;
}

.cipher-widget__subst-label {
  padding: 0;
  font-size: .8em;
}

.cipher-widget__success-indicator {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 6px;
  height: 6px;
  border-radius: 3px;
}

.cipher-widget__success-indicator_is-success {
  background-color: #00EB5F;
  box-shadow: 0 0 5px #00EB5F;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.cipher-widget_monolaphabet__input .cipher-widget__text {
  width: 100%;
  box-sizing: border-box;
}

.cipher-widget__text_higlighted {
  box-shadow: 0px 0px 5px#00EB5F;
  background-color: #00EB5F;
  color: #25342F;
  font-weight: bold;
}
.staganography-widget {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.steganography-widget__background-img,
.steganography-widget__canvas,
.steganography-widget__hidden_img {
  position: absolute;
}

.steganography-widget__hidden_img {
  z-index: -2;
}
.next-chapter-link__label.command__phrase {
  margin-top: 0;
}

.next-chapter-link__controls, .next-chapter-link__label {
  display: inline-block;
}

.next-chapter-link__controls {
  margin-left: 10px;
  border: 3px solid #25342F;
  border-radius: 3px;
}

.next-chapter-link__input, .next-chapter-link__button {
  box-sizing: border-box;
  height: 35px;
  color: #25342F;
  background-color: white;
  border: none;
}

.next-chapter-link__input {
  background-color: rgba(255, 255, 255, 0);
  width: 150px;
  font-family: "Ubuntu Mono", monospace;
  padding-left: 5px;
}


/* same as for cipher-widget, need to merge this */
 .next-chapter-link__input:focus {
  outline: none;
  box-shadow: 0 0 3px #00EB5F;
}

.next-chapter-link__button {
  width: 2.2em;
  padding: 0;
  border-left: none;
  border-radius: 0px 3px 3px 0px;
  cursor: pointer;
}

.next-chapter-link__button:hover {
  background-color: lightgrey;
}

.next-chapter-link__label_wrong {
  font-family: "Ubuntu Monon", monospace;
  font-size: .6em;
  margin-left: 10px;
  color: #ED2261;
}
.modal__background {
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background-color: rgba(0, 0, 0, .4);
  z-index: 3;
  overflow: auto;
  font-size: 1.1em;
}

.modal__content {
  background-color: white;
  padding: 40px;
  width: 500px;
  margin: 200px auto 20px auto;
  border-radius: 2px;
}

.modal__text-explanation {
  margin: 0px;
  margin-bottom: 20px;
}

.modal__btn {
  font-size: 1em;
  font-weight: bold;
  padding: 6px 10px;
  background-color: #25342F;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  color: #00EB5F;
}

.modal__btn:hover {
  background-color: rgb(45, 73, 64);
}

.modal__btn_go {
  float: right;
}
.nav__link {
  position: fixed;
  top: 0px;
  width: 50px;
  height: 100%;
  opacity: .2;
  cursor: pointer;
  background-color: lightgray;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 40px;
}

.nav__link_right {
  right: 0px;
  background-image: url(/crypto/static/media/arrow_forward.b2096af9.svg);
}

.nav__link_left {
  left: 0px;
  background-image: url(/crypto/static/media/arrow_back.3468c9c1.svg);
}

.nav__link:hover {
  opacity: .35;
}
.cipher-widget__letters-freq-cont {
  margin-bottom: 4px;
  margin-left: 24px;
}

.cipher-widget__letter-freq {
  display: inline-block;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 3px;
  padding: 0 3px;
  cursor: pointer;
}

.cipher-widget__letter-freq_locked {
  -webkit-transform: none!important;
          transform: none!important;
  cursor: no-drop;
}

.cipher-widget__letter-freq_selected {
  border: 1px solid gray;
}

.cipher-widget__letter-freq_highlighted {
  background-color: #425b53;
}

.cipher-widget__letter-freq .cipher-widget__text {
  letter-spacing: 0px;
  margin: 0;
  padding: 0 0 5px 0;
  width: 100%;
  text-align: center;
}

.cipher-widget__pair-indics-cont {
  margin-left: 24px;
  position: relative;
}

.cipher-widget__pair-indicator-outer,
.cipher-widget__pair-reset {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.cipher-widget__pair-reset {
  position: absolute;
  left: -22px;
  top: 0px;
  color: white;
  text-align: center;
}

.cipher-widget__pair-indicator-inner {
  width: 4px;
  height: 4px;
  border-radius: 4px;
  border: thin solid white;
  position: absolute;
  top: 9px;
  left: 9px;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.cipher-widget__pair-indicator-outer:hover .cipher-widget__pair-indicator-inner {
  background-color: white;
}

.cipher-widget__pair-indicator-inner_active {
  top: 0px;
  height: 100%;
  width: 1px;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.cipher-widget__pair-indicator-bar {
  stroke: #00EB5F;
  stroke-width: 2px;
}

.cipher-widget__pair-indicator-bar_plain {
  stroke: #6AE2FA;
}

.cipher-widget_monoalphabet .cipher-widget__secret-text .cipher-widget__text_plain {
  color: rgb(191, 244, 255);
}
.cipher-widget_monoalphabet .cipher-widget__secret-text
.cipher-widget__text_plain.cipher-widget__text_higlighted {
  color: #25342F;
  background-color: rgb(191, 244, 255);
  box-shadow: 0px 0px 5px rgb(191, 244, 255);
}

.cipher-widget__text_hint {
  color: white;
  margin-bottom: 14px;
}

.cipher-widget__text_hint
.cipher-widget__text {
  letter-spacing: 0;
  padding: 0;
}

.cipher-widget__letter-selector {
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #739B8E;
  border-radius: 2px;
  color: white;
  padding: 2px 5px;
  font-family: 'Ubuntu Mono', monospace;
  width: 20px;
  box-sizing: border-box;
  position: absolute;
}

.cipher-widget__letter-selector_empty {
  background-image: url(/crypto/static/media/baseline-search-24px.9e35e142.svg);
  background-repeat: no-repeat;
  background-position: top 1px left 1px;
  background-size: 16px;
}

.cipher-widget__letter-selector_bottom {
  bottom: 6px;
}

.cipher-widget__hint-button {
  cursor: pointer;
  font-family: 'Ubuntu Mono', monospace;
  font-size: .9em;
  padding: 4px;
  border: 1px solid #739B8E;
  border-radius: 2px;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  color: white;
}


.cipher-widget__hint-button:not(.cipher-widget__hint-button_inactive):hover {
  background-color: #425b53;
}

.cipher-widget__violated-conds {
  display: inline-block;
}

.cipher-widget__violated-cond {
  margin-left: 16px;
  display: inline-block;
}

.cipher-widget__hint-warning {
  display: inline-block;
  margin-left: 16px;
  font-size: .8em;
  color: #f94078;
}

.cipher-widget__hint-button_inactive {
  color: gray;
}
.kats-dialog {
  position: relative;
}

.kats-dialog__msg {
  position: absolute;
  color: #00EB5F;
}

.kats-dialog__msg_kat {
  top: 57px;
  left: 309px;
  width: 320px;
}

.kats-dialog__msg_ferdi {
  top: 172px;
  left: 167px;
  width: 320px;
}
